.styles {
  display: grid;
  grid-template-rows: min-content 1fr;

  & h1 {
    font-size: 1.5em;
    margin: 0;
    text-transform: capitalize;

    & span {
      color: var(--champion-heading-accent-color);
    }

    @media (min-width: 716px) {
      font-size: 2em;
    }
  }

  & ul {
    list-style: none;
    padding: 0;
    margin: -20px 0 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-content: flex-start;
  }

  & li {
    width: 160px;
    text-align: center;
    margin: 20px;
  }

  & li a {
    display: block;
    border-radius: var(--border-radius);
    font-weight: bold;
    padding: 50px 0;
    background-color: var(--champion-list-background-color);
    color: var(--champion-list-text-color);
    text-decoration: none;
    outline: none;

    @media (hover) {
      /* Prevent top champion box from being highlighted after clicking cancel from AddChampion */
      &:hover {
        background-color: var(--champion-list-hover-background-color);
        color: var(--champion-list-hover-text-color);
      }

      &:active {
        filter: brightness(0.9);
      }
    }

    &:focus-visible {
      outline: 4px solid var(--champion-list-focus-outline-color);
      outline-offset: 0;
    }
  }
}

.topBar {
  padding: 30px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;

  & a {
    outline-offset: 0;
  }
}
