.styles {
  composes: input from "../styles/inputs.module.css";
  background-color: var(--input-search-background-color);
  padding: 0 10px;
  width: 100%;
  display: flex;
  align-items: center;
  column-gap: 5px;

  & :global(.material-icons) {
    font-size: 24px;
    color: var(--input-search-icon-color);

    &:global(.search) {
      cursor: default;
    }

    &:global(.clear) {
      cursor: pointer;
    }
  }

  &:focus-within :global(.material-icons) {
    color: var(--input-search-icon-focus-color);
  }
}

.input {
  background: transparent;
  border: none;
  appearance: none;
  width: 100%;
  padding: 10px 0;

  &::placeholder {
    color: var(--input-placeholder-text-color);
    opacity: 1;
  }

  &:focus,
  &:active {
    outline: none;
  }
}

.clear {
  padding: 0;
  border: none;
  cursor: pointer;
  background: transparent;
  border-radius: var(--border-radius);
  color: var(--input-search-icon-color);

  & :global(.material-icons) {
    color: var(--search-input-icon-color);
    vertical-align: middle;
  }

  &:focus {
    outline: none;
  }

  &:focus-visible {
    outline: 2px solid #000;
  }
}
