.styles {
  @media (min-width: 716px) {
    background-color: var(--nav-background-color);
    border-right: 1px solid var(--nav-border-color);

    & .navContent {
      position: fixed;
      width: 240px;

      @supports (-webkit-touch-callout: none) {
        /* remove position:fixed for iOS due to rubber band effect */
        position: initial;
      }
    }
  }

  & nav {
    @media (max-width: 715px) {
      visibility: hidden;
      z-index: 2;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: var(--nav-background-color);
      display: grid;
      place-items: center;
      text-align: center;
      font-size: 36px;
      opacity: 0;

      & li a {
        display: block;
        padding: 2vw;
      }

      & .cancelBtn {
        position: absolute;
        left: 50%;
        transform: translate(-50%, 0);
        border-radius: 50%;
        bottom: 10vw;
        cursor: pointer;
        background: transparent;
        border: 0;
        padding: 0;
        color: var(--nav-item-text-color);

        &:focus,
        &:hover,
        &:active {
          background-color: var(--nav-item-hover-background-color);
          border-radius: 50%;
        }

        & span {
          font-size: 66px;
          padding: 5px;
          display: block;
        }
      }
    }

    & ul {
      list-style: none;
      padding: 0;
      margin: 0;
    }

    & li a {
      text-decoration: none;
      color: var(--nav-item-text-color);
      outline: none;
    }

    & .cancelBtn {
      @media (min-width: 716px) {
        display: none;
      }
    }

    & li a {
      display: block;
      padding: 15px 20px;

      &:hover {
        color: var(--nav-item-hover-text-color);
        background-color: var(--nav-item-hover-background-color);
      }

      &:active {
        filter: brightness(0.9);
      }

      &:global(.active) {
        color: var(--nav-item-active-text-color);
        background-color: var(--nav-item-active-background-color);
        font-weight: bold;
      }

      &:focus-visible {
        outline: 4px solid var(--nav-focus-outline-color);
        outline-offset: -4px;
      }
    }

    @nest :global(.menu-open) & {
      visibility: visible;
      opacity: 1;
      transition: opacity 0.4s ease-in-out;
    }

    @nest :global(.menu-closing) & {
      visibility: visible;
      opacity: 0;
      transition: opacity 0.4s ease-in-out 0.2s;
    }
  }
}

.appTitle {
  padding: 30px 20px;
  font-size: 2em;
  font-weight: bold;

  @media (min-width: 716px) {
    font-size: 1.5em;
    text-align: center;
  }
}
