.btn {
  padding: 10px;
  border-radius: var(--border-radius);
  border: none;
  background-color: transparent;
  cursor: pointer;
  text-decoration: none;

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }

  &:active {
    filter: brightness(0.9);
  }

  &:focus-visible {
    outline: 4px solid var(--focus-color);
  }
}

.primaryBtn {
  composes: btn;
  --focus-color: #93c5fd;
  background-color: #2563eb;
  color: #fff;

  &:hover {
    background-color: #1d4ed8;
  }
}

.dangerBtn {
  composes: btn;
  --focus-color: #fca5a5;
  background-color: #dc2626;
  color: #fff;

  &:hover {
    background-color: #b91c1c;
  }
}

.lightBtn {
  composes: btn;
  --focus-color: #d4d4d4;
  box-shadow: inset 0 0 0 1px #d4d4d4;
  background-color: #f5f5f4;

  &:hover {
    background-color: #d4d4d4;
  }

  &:focus-visible {
    box-shadow: inset 0 0 0 1px #a3a3a3;
  }
}

.clearBtn {
  composes: btn;
  --focus-color: #a3a3a3;
  background-color: transparent;

  &:hover {
    background-color: #d4d4d4;
  }
}

.closeBtn {
  composes: btn;
  --focus-color: #a3a3a3;
  background-color: transparent;

  &:hover {
    background-color: #d4d4d4;
  }
}
