.styles {
  padding: 30px 20px;

  & p {
    line-height: 1.5;
  }

  & a {
    color: var(--app-link-text-color);

    &:hover {
      text-decoration: none;
    }
  }

  & h1 {
    font-size: 1.5em;
    padding: 0 0 30px;
    margin: 0;

    & span {
      color: var(--champion-heading-accent-color);
    }

    @media (min-width: 716px) {
      font-size: 2em;
    }
  }

  & h2 {
    font-size: 1.3em;
    margin-top: 30px;
    padding-bottom: 10px;
    border-bottom: 1px solid var(--nav-border-color);

    @media (min-width: 716px) {
      font-size: 1.5em;
    }
  }

  & img {
    width: 100%;
    display: block;
  }

  @media (min-width: 716px) {
    & img {
      max-width: 50%;
      margin: 0 auto;
    }
  }
}
