.styles {
  min-height: 100%;
  padding: 0 20px 30px;
  display: grid;

  @media (min-width: 716px) {
    padding: 30px 20px 30px;
  }

  & h1 {
    font-size: 1.5em;
    padding: 0 0 30px;
    margin: 0;
    text-transform: capitalize;

    & span {
      color: var(--champion-heading-accent-color);
    }

    @media (min-width: 716px) {
      font-size: 2em;
    }
  }

  & label {
    padding-bottom: 10px;
    display: block;
  }
}

.input {
  composes: input from "../styles/inputs.module.css";
  margin-bottom: 20px;
  width: 100%;

  &:last-of-type {
    margin-bottom: 40px;
  }
}

.buttons {
  display: flex;
  column-gap: 8px;
}
