*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --border-radius: 3px;
  --transitions: color .15s ease-in-out, background-color .15s ease-in-out, border .15s ease-in-out, box-shadow .15s ease-in-out, outline-width .15s ease-in-out;
  --app-background-color: #fff;
  --app-link-text-color: #2563eb;
  --app-text-color: #000;
  --champion-heading-accent-color: #2563eb;
  --champion-list-background-color: #737373;
  --champion-list-focus-outline-color: #a3a3a3;
  --champion-list-hover-background-color: #e5e5e5;
  --champion-list-hover-text-color: #525252;
  --champion-list-text-color: #fff;
  --input-background-color: #fff;
  --input-border-color: #e5e5e5;
  --input-focus-background-color: #fff;
  --input-focus-border-color: #3b82f6;
  --input-focus-outline-color: #dbeafe;
  --input-hover-border-color: #a3a3a3;
  --input-placeholder-text-color: #a3a3a3;
  --input-search-background-color: #f5f5f5;
  --input-search-icon-color: #a3a3a3;
  --input-search-icon-focus-color: #404040;
  --nav-background-color: #f5f5f5;
  --nav-border-color: #e5e5e5;
  --nav-focus-outline-color: #2563eb;
  --nav-item-active-background-color: #dbeafe;
  --nav-item-active-text-color: #2563eb;
  --nav-item-hover-background-color: #e5e5e5;
  --nav-item-hover-text-color: #000;
  --nav-item-text-color: #000;
  --spinner-background-color: #e0e0e0;
  --spinner-border-color: #2563eb;
}

